@import "sass/_mixins";
@import "sass/_variables";


@include mobile {
    .header {
        margin: 0 auto;
        max-width: 320px;

        &__logo {
            width: 100%;
            height: 540px;
            background: url(../image/logo.png) 0 0 no-repeat;
            background-position: center center;

            .log__in {
                margin: 33px 40px 0 84px;
                display: inline-block;
                color: white;
                font-family: 'montserratbold' Arial , sans-serif;
                font-size: 14px;
                text-decoration: none; }

            .log__in:hover {
                margin: 20px 21px -12px 65px;
                padding: 11px 17px;
                border: 2px solid white;
                @include border-radius(30px); }

            .sign__up {
                color: white;
                font-family: 'montserratbold' Arial , sans-serif;
                font-size: 14px;
                text-decoration: none; }

            .sign__up:hover {
                margin: 20px 0 -12px -19px;
                padding: 11px 17px;
                border: 2px solid white;
                @include border-radius(30px); }

            .title {
                margin: 128px 0 0 70px;
                display: block;
                color: white;
                font-family: 'montserratbold' Arial , sans-serif;
                font-size: 20px;
                letter-spacing: 1.5px; }

            .info {
                margin: 30px 0 0 0;
                display: block;
                color: white;
                font-family: 'montserratbold' Arial , sans-serif;
                font-size: 30px;
                line-height: 1.2;
                text-align: center;
                text-transform: uppercase; }

            .find {
                margin: 11px 0 0 50px;
                display: block;
                width: 220px;
                color: #ffffff;
                font-family: 'robotolight', Arial, sans-serif;
                font-size: 18px;
                text-align: center; }

            .search {
                margin: 0 auto;
                margin-top: 18px;
                padding: 12px 30px;
                display: block;
                color: white;
                font-family: 'montserratbold' Arial , sans-serif;
                width: 200px;
                background-color: #ff4e50;
                border: 1px solid transparent;
                @include border-radius(30px);
                font-size: 14px;
                text-align: center;
                text-decoration: none; }

            .search:hover {
                background-color: white;
                color: #ff4e50; } } } }

