@import "sass/_mixins";
@import "sass/_variables";
@import "sass/sprite";

@include mobile {
    .footer {
        margin: 0 auto;
        margin-top: 24px;
        max-width: 320px;

        &__line {
            margin: 0 auto;
            height: 4px;
            background-color: #ff4e50; }

        &__title {
            margin: 0 auto;
            margin-top: 17px;
            display: block;
            width: 142px;
            height: 16px;
            color: #ff4e50;
            font: 16px 'montserratbold' Arial , sans-serif;
            font-weight: 700;
            text-transform: uppercase; }

        &__company {
            margin: 0 auto;
            margin-top: 14px;
            margin-bottom: 25px;
            display: block;
            width: 67px;
            height: 13px;
            color: #333;
            font: 14px 'montserratbold' Arial , sans-serif;
            font-weight: 700; } }

    nav {
        margin-top: -117px;
        display: block; }

    .link {
        margin: 0 auto;
        margin-bottom: 10px;
        display: inline-flex;
        height: 27px;
        color: #666;
        font: 14px 'robotolight', Arial, sans-serif;
        font-weight: 300;
        line-height: 24px;

        .item__first {
            padding: 3px 0 0 62px;
            cursor: pointer; }

        .item__second {
            padding: 3px 0 0 12px;
            cursor: pointer; }

        .item__third {
            padding: 3px 0 0 14px;
            cursor: pointer; }

        .item__fourth {
            padding: 3px 0 0 13px;
            cursor: pointer; } }

    a {
        text-decoration: none; }

    .footer__partners {
        margin: 0 auto;
        padding-top: 12px;
        display: block;
        width: 59px;
        height: 10px;
        color: #333;
        font: 14px 'montserratbold' Arial , sans-serif;
        font-weight: 700; }

    .footer__created {
        margin: 0 auto;
        padding-top: 20px;
        display: block;
        height: 50px; }

    .footer__designed {
        margin: 0 auto;
        display: block;
        width: 79px;
        height: 11px;
        color: #ccc;
        font: 10px 'montserratbold' Arial , sans-serif;
        font-weight: 700;
        letter-spacing: 0.9px;
        text-transform: uppercase; }

    .footer__logo {
        margin: 9px 0 0 103px;
        display: block;
        @include sprite($five); } }

